/** @format */

import React from 'react';
const MainFrameLazy = React.lazy(() => import('../pages/MainFrame'));
const CartLazy = React.lazy(() => import('../pages/common/Cart'));
const DetailLazy = React.lazy(() => import('../pages/common/Detail'));
const HomeLazy = React.lazy(() => import('../pages/common/Home'));
const ProductLazy = React.lazy(() => import('../pages/common/Product'));
const ProfileLazy = React.lazy(() => import('../pages/common/Profile'));
const OrderLazy = React.lazy(() => import('../pages/common/Order'));
const CheckoutLazy = React.lazy(() => import('../pages/common/Checkout'));
const SearchLazy = React.lazy(() => import('../pages/common/Search'));
const BackFrameLazy = React.lazy(() => import('../pages/BackFrame'));
const CreateLazy = React.lazy(() => import('../pages/manage/Create'));
const EditUserLazy = React.lazy(() => import('../pages/manage/EditUser'));
const EditProductLazy = React.lazy(() => import('../pages/manage/EditProduct'));

export const routes = [
    {
        path: '/',
        breadcrumbName: 'Home',
        element: <MainFrameLazy />,
        children: [
            {
                path: '/',
                breadcrumbName: 'Home',
                element: <HomeLazy />,
            },
            {
                path: 'user/cart',
                breadcrumbName: 'Cart',
                element: <CartLazy />,
            },
            {
                path: 'user/profile',
                breadcrumbName: 'Profile',
                element: <ProfileLazy />,
            },
            {
                path: 'user/order',
                breadcrumbName: 'Order',
                element: <OrderLazy />,
            },
            {
                path: 'user/checkout',
                breadcrumbName: 'Checkout',
                element: <CheckoutLazy />,
            },
            {
                path: 'search',
                breadcrumbName: 'Search',
                element: <SearchLazy />,
            },
            {
                path: '*',
                element: <HomeLazy />,
            },
        ],
    },
    {
        path: '/manage',
        breadcrumbName: 'Home',
        element: <BackFrameLazy />,
        children: [
            {
                path: '/manage',
                element: <EditUserLazy />,
            },
            {
                path: 'products',
                element: <EditProductLazy />,
            },
            {
                path: 'users',
                element: <EditUserLazy />,
            },
            {
                path: 'create',
                element: <CreateLazy />,
            },
            {
                path: '*',
                element: <EditUserLazy />,
            },
        ],
    },
];

let categoryArray = ['men', 'women', 'kid', 'other'];

categoryArray.forEach(category => {
    const productRoute = {
        key: category,
        path: `/products/${category}`,
        breadcrumbName: category,
        element: <ProductLazy />,
        children: [
            {
                path: `:id`,
                breadcrumbName: 'Detail',
                element: <DetailLazy />,
            },
        ],
    };
    routes[0].children.push(productRoute);
});
